@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
html{margin:0;font-family:Roboto, sans-serif}.selectedItemsList{display:flex;flex-direction:column;width:20vw;text-transform:uppercase;font-size:20px}.itemAndButton{width:20vw;display:grid;grid-template-columns:60% 40%;margin-bottom:10px}.item{list-style-type:none;line-height:30px;padding-left:10px;background-color:brown;color:white}.itemSelected{background-color:#0e7c00}.button{color:white;background-color:#7c7c85;border:solid 1px #7c7c85}.button--top{margin-top:10px}

